.head {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  display: flex;
  // opacity: 0.8;
  // background: #fff;
  box-sizing: border-box;
  z-index: 10000;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: inherit;
    background-attachment: fixed;
    filter: blur(4px);
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.8);
  }
  // border-bottom: 1px solid #ccc;
  .left {
    flex: 1;
    display: flex;
    padding: 0 100px;
    height: 70px;
    z-index: 1;
    // background: rgba(255, 255, 255, 1);
  }
  // .right {
  //   width: 100px;
  //   // background: rgba(255, 255, 255, 1);
  // }
  // .right-ac {
  //   background: rgba(255, 255, 255, 0.8);
  // }
  .logo {
    width: 100px;
    height: 45px;
    background: url(../assert/logo.png);
    background-size: 100% 100%;
    margin-top: 10px;
  }
  .menu {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    .item {
      color: #666;
      line-height: 70px;
      padding: 0 30px;
      cursor: pointer;
      &:hover {
        color: #000;
      }
    }
    .active {
      box-sizing: border-box;
      height: 70px;
      border-bottom: 5px solid #0496f2;
      color: #000;
    }
  }
}

@media (max-width: 1336px) {
  .head {
    .left {
      padding: 0 40px;
    }
  }
}

@media screen and (min-width: 1919px) {
  .head {
    height: 100px;
    font-size: 20px;
    .left {
      padding: 0 200px;
      height: 100px;
    }
    .logo {
      width: 130px;
      height: 50px;
      background: url(../assert/logo.png);
      background-size: 100% 100%;
      margin-top: 25px;
    }
    .menu {
      .item {
        line-height: 100px;
      }
      .active {
        height: 100px;
      }
    }
  }
}
